import React, { Component } from "react";
import { BrowserRouter, Route, Redirect } from "react-router-dom";

import ExerciseWrapper from "./ExerciseWrapper/ExerciseWrapper";
import Menu from "./Menu";
import Overview from "./Overview";
import Main from "./Main";
import WorkoutCreator from "./WorkoutCreator";
import ExercisePreview from "./ExercisePreview/ExercisePreview";
import Settings from "./Settings";
import WgerSearch from "./ExerciseSearch/WgerSearch";
import InternalSearch from "./ExerciseSearch/InternalSearch";
import { loadDefaultData } from "../actions";
import { connect } from "react-redux";
class App extends Component {
  componentDidMount() {
    //Lade, falls ntig, die Standarddaten aus JSON-Dateien und wger-Datenbank
    if (this.props.muscles.length === 0) {
      this.props.loadDefaultData();
    }
  }
  render() {
    return (
      <BrowserRouter>
        <Route path="/" component={Menu} />
        <div className="content">
          {/*Da die Github Seite als Root /project hat, muss diew auf meine Hauptseite gelenkt werden (/)*/}
          <Route path="/project">
            <Redirect to="/" />
          </Route>
          <Route path="/overview" component={Overview} />
          <Route path="/workout" component={ExerciseWrapper} exact />
          <Route path="/workout/:id" component={WorkoutCreator} />
          <Route
            path="/exercise/:id"
            component={ExercisePreview}
            onLeave={() => alert("hi")}
          />
          <Route path="/exercise/search" component={InternalSearch} />
          <Route path="/settings" component={Settings} />
          <Route path="/wger" component={WgerSearch} />
          <Route path="/" component={Main} exact />
        </div>
      </BrowserRouter>
    );
  }
}

export default connect(
  state => {
    return { muscles: state.wger.muscles };
  },
  { loadDefaultData }
)(App);
